import { parseISO, format } from 'date-fns';
import { AsyncAppAction, AsyncAppDispatch, RootState } from '..';
import { filterData, transaction } from '../../types/transaction';
import { loading } from '../../utils/general';
import http from '../../utils/http';
import {
  UpdateTransactionList,
  setTransactionDetail,
  UpdateTransactionLoading,
} from '../actionCreators/transaction';

/**
 * Get transaction list
 * @param {string} accountId
 * @param {number} limit
 * @param {number} offset
 * @param {string} query
 * @returns {*}
 */
export const getTransactions = (
  accountId: string,
  accountType: 'cash' | 'crypto',
  limit: number,
  offset: number,
  query?: string,
  filters?: filterData,
  type?: 'json' | 'pdf'
): AsyncAppAction<{
  data: transaction[];
  total: number;
}> => (dispatch: AsyncAppDispatch, getState: () => RootState) => new Promise((resolve, reject) => {
  let url = accountType === 'cash' ? `v1/account/${accountId}/transaction?limit=${limit}&offset=${offset}` : `v1/crypto/${accountId}/transaction?limit=${limit}&offset=${offset}`;

  if (type) {
    url += `&export=${type}`;
  }
  if (query && query.length > 0) {
    url += `&query=${encodeURIComponent(query)}`;
  }
  if (filters) {
    const f: any = filters;
    Object.keys(filters).forEach((key) => {
      if (f[key]) {
        if (accountType === 'crypto' && key === 'startDate') {
          url += `&startTime=${f[key]}`;
        } else if (accountType === 'crypto' && key === 'endDate') {
          url += `&endTime=${f[key]}`;
        } else {
          url += `&${key}=${f[key]}`;
        }
      }
    });
  }

  setTimeout(() => {
    if (type === 'pdf') {
      loading(true);
    } else {
      dispatch(UpdateTransactionLoading(true));
    }
  }, 0);
  http
    .get<{
          data: transaction[];
          total: number;
        }>(url, {
          responseType: type === 'pdf' ? 'blob' : 'json'
        })
    .then(({ data }) => {
      if (type !== 'pdf') {
        let txns = getState().transaction.txnList;
        let { transactions } = getState().transaction;
        if (offset === 0) {
          txns = {};
          transactions = [];
        }
        const newTxn = data.data;
        newTxn.forEach((txn) => {
          const txnDate = parseISO(txn.txnDate);
          const month = format(txnDate, 'MMMM yyyy');
          if (txns[month]) {
            txns[month].push(txn);
          } else {
            txns[month] = [txn];
          }
          transactions.push(txn);
        });

        dispatch(
          UpdateTransactionList({ transactions, txnList: txns, total: data.total }),
        );
      }
      resolve(data);
    })
    .catch(reject)
    .finally(() => {
      if (type === 'pdf') {
        loading(false);
      } else {
        dispatch(UpdateTransactionLoading(false));
      }
    });
});

/**
 * Get transaction details by id
 * @param {string} accountId
 * @param {string} txnId
 * @returns {*}
 */
export const getTransactionDetail = (
  accountId: string,
  accountType: string,
  txnId: string,
  type: 'json' | 'pdf' = 'json'
): AsyncAppAction<transaction> => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  loading(true);
  const url = accountType === 'crypto' ? `v1/crypto/${accountId}/transaction/${txnId}` : `v1/account/${accountId}/transaction/${txnId}?export=${type}`;

  http
    .get<transaction>(url, {
      responseType: type === 'pdf' ? 'blob' : 'json'
    })
    .then(({ data }) => {
      if (type !== 'pdf') {
        dispatch(setTransactionDetail(data));
      }
      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      loading(false);
    });
});
