import React, {
  useCallback, useEffect, useMemo, useRef, useState
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, Center, Input, List, ListItem, HStack, Divider, Tag, TagLabel, TagCloseButton
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import InfiniteScroll from 'react-infinite-scroll-component';
import { format } from 'date-fns';
import { useNavigate } from 'react-router-dom';
import Icon from '../../../components/Icon';
import style from './TransactionsList.module.css';
import { filterData as filterDataType, transaction } from '../../../types/transaction';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { useDebouncedEffect } from '../../../hooks/app';
import { apiErrorHandler, currencyFormat } from '../../../utils/general';
import SkeletonLoader from '../../../components/SkeletonLoader';
// import MiniBox from '../../../layout/MiniBox';
import Loader from '../../../components/Loader';
import { getTransactions } from '../../../store/middlewares/transaction';
import { updateTransactionFilter } from '../../../store/actionCreators/transaction';
import { useDrawer } from '../../../hooks/drawer';
import { eventEmitter, Events } from '../../../utils/events';

const TransactionFilter = React.lazy(() => import('../TransactionFilter/Index'));

interface Props {
  showFilter?: boolean,
  showExport?: boolean,
  showMonth?: boolean,
  scrollBoxHeight?: number,
  showSearch?: boolean;
  showCompactView?: boolean;
  accountType : 'cash' | 'crypto',
  filterRequest?: {
    type: 'contact' | 'card',
    id: string
  } | null
  transactionsPerView?: number
  dateFormat: string

}
// TODO: Needs to move this component into component folder
const TransactionItem = React.memo(({ transactionData, dateFormat }: {
  transactionData: transaction
  dateFormat: string
}) => {
  const { t } = useTranslation();
  const { open } = useDrawer();
  const appConfig = useAppSelector((store) => store.core.ui, shallowEqual);
  const txnDate = useMemo(() => format(new Date(transactionData.createdAt), dateFormat), [dateFormat, transactionData.createdAt]);

  const formattedAmount = useMemo(() => {
    const amount = transactionData.walletId ? transactionData.amount : currencyFormat(transactionData.amount);
    if (parseFloat(transactionData.amount) > 0) {
      return `+${amount}`;
    }
    return amount;
  }, [transactionData]);

  const amountColor = useMemo(() => {
    if (transactionData.status === 'pending') {
      return '#FFAC4A';
    } if (transactionData.status === 'canceled') {
      return '#FF4A4A';
    }
    return `${transactionData.txnType === 'debit' ? appConfig.modules.transactions?.amountSentColor : appConfig.modules.transactions?.amountReceivedColor}`;
  }, [transactionData]);

  return (
  // (Update loader) Loader to be updated
  // <SkeletonLoader name="TransactionList" />
    <ListItem
      backgroundColor="var(--bg-white)"
      data-class="transaction-list-item"
      data-testid={`transaction-item-${transactionData.id}`}
      onClick={() => {
        open(React.lazy(() => import('../TransactionDetails/Index')), { id: transactionData.id });
      }}
      className={style.transactions__list_item}
    >
      <Box className={`${style.tag} ${style[`tag__${transactionData.transferType}`]}`}>
        <Text textTransform="capitalize" variant="small" textAlign="center" color="inherit" textOverflow="ellipsis" overflow="hidden">{t([`transactions_list.${transactionData.transferType}`, transactionData.transferType || ''])}</Text>
      </Box>
      <Box className={style.transaction__description}>
        <Text variant="regular" className="text-dark" textAlign="left">{transactionData.title}</Text>
        <Text variant="small" className="text-light" textAlign="left">{transactionData.status === 'pending' ? 'Pending' : txnDate}</Text>
      </Box>
      <Text variant="small" title={`${currencyFormat(transactionData.amount)}`} textAlign="right" fontWeight={appConfig.modules.transactions?.amountFontWeight} color={amountColor}>
        {formattedAmount}
      </Text>
    </ListItem>
  );
});

const TransactionsList = ({
  showFilter, showExport, showMonth, scrollBoxHeight, showSearch, showCompactView, accountType, filterRequest: filterData, transactionsPerView = 20, dateFormat
}: Props) => {
  const { t } = useTranslation();
  const { open } = useDrawer();
  const navigate = useNavigate();
  const [searchKeyword, updateSearch] = useState('');
  const [keyword, updateKeyword] = useState('');
  const offset = useRef(0);
  const isSeachChanged = useRef(false);
  const dispatch = useAppAsyncDispatch();

  const isLoading = useAppSelector((store) => store.transaction.transactionLoading, shallowEqual);
  const transactions = useAppSelector((store) => store.transaction.transactions, shallowEqual);
  const data = useAppSelector((store) => store.transaction.txnList, shallowEqual);
  const totalCount = useAppSelector((store) => store.transaction.total, shallowEqual);
  const account = useAppSelector((store) => store.account.selectedAccount, shallowEqual);
  const cryptoAccount = useAppSelector((store) => store.crypto.selectedAccount, shallowEqual);
  const filtersFromStore = useAppSelector((store) => store.transaction.filters, shallowEqual);

  const currentDataCount = transactions.length;
  const filters = useMemo(() => ({ ...filtersFromStore.data, timePeriod: filtersFromStore.tmpStates.timePeriod }), [filtersFromStore]);
  const page = (offset.current / transactionsPerView) + 1;

  useDebouncedEffect(() => {
    offset.current = 0;
    updateKeyword(searchKeyword);
  }, 1000, [searchKeyword]);

  const countRef = useRef(1);

  const fetchTransactions = useCallback((responseFormat: 'json' | 'pdf' = 'json', filterOptions:filterDataType = { ...filters }) => {
    console.log('called');
    const accountId = accountType === 'cash' && account ? account.id : (accountType === 'crypto' && cryptoAccount ? cryptoAccount.id : '');
    if (accountId !== '') {
      // filter based on sdk params

      const f = JSON.parse(JSON.stringify(filterOptions));
      if (filterData && filterData.type && filterData.id) {
        if (filterData.type === 'contact') {
          f.contactId = filterData.id;
        } else if (filterData.type === 'card') {
          f.cardId = filterData.id;
        }
      } else {
        f.contactId = undefined;
        f.cardId = undefined;
      }

      if (f.txnType === 'card') {
        f.txnType = undefined;
        f.transferType = 'card';
      }

      // check max amount
      if (f.maxAmount === '0.00' || f.maxAmount === '0') {
        delete f.maxAmount;
      }
      countRef.current += 1;
      if (countRef.current <= 5) {
      /**
       * get transactions list
       */
        dispatch(getTransactions(accountId, accountType, responseFormat === 'pdf' ? 100 : transactionsPerView, responseFormat === 'pdf' ? 0 : offset.current, keyword, f, responseFormat))
          .then((res:any) => {
            if (responseFormat === 'pdf') {
              const url = window.URL.createObjectURL(res);
              const a = document.createElement('a');
              a.href = url;
              a.download = 'statement.pdf';
              a.click();
              a.remove();
            }
          })
          .catch(apiErrorHandler);
      } else {
        console.log('count', countRef.current);
      }
    }
  }, [account, keyword, offset, transactionsPerView, filters, filterData, countRef]);

  useEffect(() => {
    fetchTransactions();
  }, [keyword, account]);

  useEffect(() => {
    const unsubscribe = eventEmitter.subscribe(Events.TRANSACTION_REFRESH, () => {
      offset.current = 0;
      fetchTransactions();
    });
    return unsubscribe;
  }, [filters]);

  const viewAll = useCallback(() => {
    navigate('/transactions');
  }, []);

  /**
   * callback for filter clear
   */
  const onClearFilter = useCallback((name: 'txnType' | 'amount' | 'timePeriod') => {
    // eslint-disable-next-line @typescript-eslint/no-shadow
    const { data, tmpStates }: {data: filterDataType, tmpStates: any} = JSON.parse(JSON.stringify({ ...filtersFromStore }));

    if (name === 'txnType') {
      data.txnType = undefined;
      data.transferType = undefined;
      tmpStates.transactionType = undefined;
    } else if (name === 'amount') {
      data.minAmount = undefined;
      data.maxAmount = undefined;
      tmpStates.amount = {
        min: '',
        max: ''
      };
    } else if (name === 'timePeriod') {
      data.startDate = undefined;
      data.endDate = undefined;
      tmpStates.startDate = undefined;
      tmpStates.endDate = undefined;
      tmpStates.timePeriod = undefined;
    }

    dispatch(updateTransactionFilter({
      data,
      tmpStates
    }));
    setTimeout(() => {
      fetchTransactions('json', data);
    }, 0);
  }, [filtersFromStore]);

  const listUI = () => Object.keys(data).map((monthName) => (
    <Box key={`tr-${monthName}`} data-testid={`month-group-${monthName.replace(/ /g, '_')}`}>
      {showMonth && <Text data-testid="month-name-text" variant="large" className={style.transactions__month_header}>{monthName}</Text>}
      <List className={style.transactions__list_month} data-class="transaction-list">
        {data[monthName].map((tr) => (
          <TransactionItem
            transactionData={tr}
            key={tr.id}
            dateFormat={dateFormat}
          />
        ))}
      </List>
    </Box>
  ));

  const txnTypeLocales = useMemo(() => ({
    credit: t('filter.credits'),
    debit: t('filter.debits'),
    card: t('filter.cardTransactions')
  }), []);

  return (
    <Box className={style.bodyStyles}>
      {/* TODO: skeleton loader should have correct loading function */}
      {isLoading && page === 1 && !isSeachChanged.current ? (
        <SkeletonLoader name="TransactionListView" showHeader={!showCompactView} />
      ) : (
        <Box width="100%" className={style.transaction__box}>
          <div>
            <HStack width="100%">
              {/* Heading */}
              <Text data-testid="heading" variant="large" mr="auto">{t('transactions_list.title')}</Text>
              {showCompactView && totalCount !== 0
                ? (
                  <Text variant="regular" className="text-light" fontSize="16px" cursor="pointer" onClick={viewAll}>
                    {t('transactions_list.viewAll')}
                    <Icon style={{ marginLeft: '8px' }} icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                  </Text>
                ) : null }
              {showExport ? (
                <HStack ml="auto" cursor="pointer" onClick={() => fetchTransactions('pdf')} data-testid="export-icon">
                  <Icon icon="icon-export" size={16} />
                  <Text variant="regular" className="transactions__export-title">{t('transactions_list.exportTitle')}</Text>
                  <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                </HStack>
              ) : null}
            </HStack>
            <HStack my={showSearch || showFilter ? '16px' : '0px'}>
              {/* Input and search */}
              {showSearch
                ? (
                  <Box className={style.transactions__search}>
                    <Icon icon="icon-search" className={style.transactions__search__icon} size={18} />
                    <Input
                      data-testid="search-input"
                      placeholder={t('transactions_list.searchTransactions')}
                      variant="primary"
                      className={style.transactions__search__input}
                      value={searchKeyword}
                      onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                        isSeachChanged.current = true;
                        updateSearch(e.target.value);
                      }}
                    />
                  </Box>
                ) : null }
              {showFilter ? (
                <Center
                  className={`${style.transactions__filter}`}
                  data-testid="filter-handler"
                  onClick={() => {
                    open(TransactionFilter);
                  }}
                >
                  <Icon icon="icon-filter" size={20} />
                </Center>
              ) : null}
            </HStack>
          </div>

          {/* Filter tags */}
          <HStack spacing={4} marginBottom="16px">
            {/* Transaction Type */}
            {filters.txnType ? (
              <Tag
                variant="primary"
                padding="10px"
                data-testid="filter-tag-txnType"
              >
                <TagLabel fontSize="12px" textTransform="uppercase">
                  {txnTypeLocales[filters.txnType]}
                </TagLabel>
                <TagCloseButton onClick={() => {
                  if (onClearFilter) { onClearFilter('txnType'); }
                }}
                />
              </Tag>
            ) : null }

            {/* Amount */}
            {(filters.minAmount && filters.minAmount !== '0.00') || (filters.maxAmount && filters.maxAmount !== '0.00')
              ? (
                <Tag
                  variant="primary"
                  padding="10px"
                  data-testid="filter-tag-amount"
                >
                  <TagLabel fontSize="12px" textTransform="uppercase">
                    {
                      filters.minAmount
                      && filters.maxAmount
                      && filters.minAmount !== '0.00'
                      && filters.maxAmount !== '0.00' ? (
                          `${currencyFormat(filters.minAmount)} - ${currencyFormat(filters.maxAmount)}`
                        ) : (
                          filters.minAmount && filters.minAmount !== '0.00'
                            ? currencyFormat(filters.minAmount) : `${currencyFormat('0.00')} - ${currencyFormat(filters.maxAmount || '')}`
                        )
                  }
                  </TagLabel>
                  <TagCloseButton onClick={() => {
                    if (onClearFilter) { onClearFilter('amount'); }
                  }}
                  />
                </Tag>
              )
              : null}

            {/* Date range */}
            {
              filters.timePeriod && (filters.timePeriod !== 'custom' || ((filters.startDate && filters.startDate !== '') || (filters.endDate && filters.endDate !== ''))) ? (
                <Tag
                  variant="primary"
                  padding="10px"
                  data-testid="filter-tag-time"
                >
                  <TagLabel fontSize="12px" textTransform="uppercase">
                    {
                    filters.timePeriod === 'custom' ? (
                      filters.startDate && filters.endDate && filters.startDate !== '' && filters.endDate !== '' ? `${filters.startDate} - ${filters.endDate}` : (
                        filters.startDate && filters.startDate !== '' ? filters.startDate : filters.endDate
                      )
                    )
                      : t(`filter.${filters.timePeriod}`)

                  }
                  </TagLabel>
                  <TagCloseButton onClick={() => {
                    if (onClearFilter) { onClearFilter('timePeriod'); }
                  }}
                  />
                </Tag>
              ) : null
            }
          </HStack>

          {/* Transactions List */}
          {
          isLoading && isSeachChanged.current ? <SkeletonLoader name="TransactionListView" showHeader={false} />
            : (
              <div
                className={style.scrollableDiv}
                id="scrollableDiv"
              >
                {showCompactView ? listUI()
                  : (
                    <InfiniteScroll
                      scrollThreshold={0.6}
                      height={scrollBoxHeight}
                      dataLength={currentDataCount}
                      next={() => {
                        offset.current = (page) * transactionsPerView;
                        setTimeout(() => {
                          console.log('call from infi');
                          fetchTransactions();
                        }, 0);
                      }}
                      hasMore={currentDataCount < totalCount && totalCount !== 0}
                      loader={<Center my={3}><Loader showModal={false} show /></Center>}
                      endMessage={(
                isLoading ? null
                  : (
                    <>
                      {totalCount !== 0
                        ? <Divider className="no_transaction" /> : null }
                      <Center py={2}>
                        <Text variant="regular" textAlign="center" className="text-light">
                          {t(totalCount === 0 ? 'transactions_list.noData' : 'transactions_list.noMoreTransactions')}
                        </Text>
                      </Center>
                    </>
                  )
              )}
                      scrollableTarget="scrollableDiv"
                    >
                      {listUI()}
                    </InfiniteScroll>
                  ) }
              </div>
            )
          }
        </Box>
      )}

    </Box>
  );
};

export default TransactionsList;
